@import url(https://fonts.googleapis.com/css?family=Roboto:300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
ul {
  margin: 0;
  padding: 0;
}

main {
  display: block;
}

h1,
h2,
h3,
h4 {
  font-size: inherit;
}

img {
  width: 100%;
  height: auto;
  border: 0;
}
.navbar {
  display: flex;
  justify-content: space-between; }

.navbar-brand, .navbar-link {
  padding: 0.8em;
  padding-left: 1em;
  padding-right: 1em;
  color: #D1E8E2;
  text-decoration: none;
  flex-grow: 1;
  background-color: #2C3531;
  z-index: 2;
  text-align: center; }

.navbar-brand {
  font-weight: 700;
  text-align: left;
  flex-grow: 0;
  padding-left: 2em;
  padding-right: 2em; }

.navbar-brand:hover, .navbar-link:hover {
  background-color: rgba(44, 53, 49, 0.8); }

.logout-button {
  padding: 0.8em;
  padding-left: 2em;
  padding-right: 2em;
  background-color: #D1E8E2;
  text-decoration: none;
  flex-grow: 1;
  color: #2C3531;
  outline: none;
  border: none;
  z-index: 2;
  text-align: center;
  font-size: 1em; }

.logout-button:hover {
  background-color: rgba(44, 53, 49, 0.8);
  color: #D1E8E2;
  cursor: pointer; }

.DS-container {
  position: relative;
  background-Color: blue;
  border-radius: 50%; }

.DS-offset {
  width: 100%;
  height: 100%; }

.App {
  width: 100vw;
  height: 100vh; }

.datapoint {
  position: relative;
  border-radius: 50%; }

.datapoint-rotation-container {
  position: relative;
  background-color: lime; }

body {
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #D1E8E2; }

.login-page {
  width: 360px;
  padding: 8% 0 0;
  margin: auto; }

.form {
  position: relative;
  z-index: 1;
  background: #FFFFFF;
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(44, 53, 49, 0.2), 0 5px 5px 0 rgba(44, 53, 49, 0.24); }

.form input {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px; }

.form button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #2C3531;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer; }

.form button:hover, .form button:active, .form button:focus {
  background: rgba(44, 53, 49, 0.8); }

.form .message {
  margin: 15px 0 0;
  color: #739a90;
  font-size: 12px; }

.form .message a {
  color: #00c393;
  font-weight: 700;
  text-decoration: none; }

.form .register-form {
  display: none; }

.container {
  position: relative;
  z-index: 1;
  max-width: 300px;
  margin: 0 auto; }

.container:before, .container:after {
  content: "";
  display: block;
  clear: both; }

.container .info {
  margin: 50px auto;
  text-align: center; }

.container .info h1 {
  margin: 0 0 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 300;
  color: #2C3531; }

.container .info span {
  color: #4d4d4d;
  font-size: 12px; }

.container .info span a {
  color: #000000;
  text-decoration: none; }

.container .info span .fa {
  color: #EF3B3A; }

.login-form > h3, .register-form > h3 {
  margin-bottom: 1em; }

.make-collection-link-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 2.6em); }

.make-collection-link {
  text-align: center;
  font-size: 1.3em;
  color: #D1E8E2;
  padding: 0.8em;
  border-radius: 0.2em;
  background-color: #2C3531;
  text-decoration: none; }

.data-collection {
  display: flex;
  width: calc(100% - 2em);
  margin: 1em;
  background-color: #2E5D48;
  color: #D1E8E2;
  align-items: center;
  -webkit-transition: background-color 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out; }

.data-collection:hover {
  background-color: #2C3531; }

.data-collection-name {
  flex-basis: 30%;
  padding: 1em; }

.data-collection-metrics {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  text-align: center;
  cursor: pointer; }

.highlighted-collection {
  background-color: #26ab70 !important; }

.metrics-display {
  display: flex;
  justify-content: space-around; }

.metrics-display > .recall {
  flex-grow: 1;
  background-color: #26ab70;
  padding: 0.4em;
  color: white;
  text-align: center; }

.metrics-display > .recall-title {
  flex-grow: 1;
  background-color: #2E5D48;
  font-weight: 700;
  padding: 0.4em;
  color: white; }

.recall-data {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 0.1);
  padding: 0.5em; }

.practice {
  padding: 1em;
  flex-basis: 30%;
  text-align: center;
  cursor: pointer; }

.demo-container {
  width: 100%;
  display: flex;
  justify-content: center; }

.demo-datapoint {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin-bottom: 1em;
  background-color: #8ea7a0;
  background-size: cover; }

